@import "./variables";
@import "./reet-style";
@import "./_base";
@import "./common";

// App.jsx
.app {}

.app_result {}

// --------------------------------------------------
// layouts
// --------------------------------------------------
// Header.jsx
.header {
  background: $backColor;
  height: $headerHeight; }
.header_title {
  width: $headerTitleWidth; }


// ----- フッター[Footer.jsx] -----
.footer {
  // テキストをボックス中央にする
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $backColor;
  width: 100%;
  height: $footerHeight;
  & > p {
    display: block;
    width: 100%;
    color: $footerFontColor;
    text-align: center; } }

// ----- トップ画像[Logo.jsx] -----
.logo {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: max-content;
  padding-top: 20px;
  & > img {
    display: block; }
  & > .logo_img {
    -webkit-animation: logo 8s ease-in-out 0s infinite normal;
    animation: logo 8s ease-in-out 0s infinite normal;
    transform-origin: center center; } }

@keyframes logo {
  0% {
    transform: rotate3d(0, 0, 0, 0deg); }
  33% {
    transform: rotate3d(0, 0, 0, 0deg); }
  100% {
    transform: rotate3d(-1, -1, -1, 360deg); } }

@-webkit-keyframes logo {
  0% {
    -moz-transform: rotate3d(0, 0, 0, 0deg);
    -ms-transform: rotate3d(0, 0, 0, 0deg); }
  33% {
    -moz-transform: rotate3d(0, 0, 0, 0deg);
    -ms-transform: rotate3d(0, 0, 0, 0deg); }
  100% {
    -moz-transform: rotate3d(-1, -1, -1, 360deg);
    -ms-transform: rotate3d(-1, -1, -1, 360deg); } }

// DrIcon.jsx
.icon {}

// --------------------------------------------------
// pages
// --------------------------------------------------
// ----- トップページ[Top.jsx] -----
.top_page {
  background-image: url("../images/top/dna_top.jpg"); }

// ----- 診断結果ページ[Result.jsx] -----
.result_page {
  background: #6f8a9c; }
.message_area {}

// --------------------------------------------------
// ui
// --------------------------------------------------
// ----- ニックネーム入力フォーム[TopSendName.jsx] -----
.from_name {
  background: $formBackColor;
  border: solid 5px $mainColor;
  border-radius: 10px;
  width: 90%;
  margin: 30px auto 50px;
  padding: 15px 0;
  text-align: center; }
// ラベル
.label_name {
  font-size: 14px;
  font-weight: bold; }
// 入力フォーム
input {
  & > .input_name {}
  height: 3em;
  display: block;
  margin: 10px auto; }
// ボタン
button.send_name {
  width: 150px;
  font-size: $fontSize;
  color: $formButtonFontColor;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  display: block;
  padding: 8px 0 10px;
  margin: 0 auto;
  background-color: $BtnBackColor;
  border-radius: 5px;
  &[disabled] {
    background-color: $BtnDisabledBackColor; } }


